.card{
    border: 1px solid #e7e7e7;
    background: linear-gradient(135deg, rgb(0, 119, 238), #06c766);
    color: #fff;
    margin: 10px;
    padding: 10px;
    display: inline-block;
    text-align: center;
    border-radius: 8px;
}
.card img{
    width: 180px;

}
.card button{
    background: linear-gradient(135deg, #eea300, #ee5700);
    margin: 0.5rem 0;
    padding: 8px 28px;
    outline: none;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: 700;
    color: #fff;
    border: none;
    cursor: pointer;
    transition: all 0.1ms ease-in-out;
}
.emptable{width: 80%;}
.emptable th{
    padding: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
}
.emptable td{
    border: 1px solid #ddd;
    padding: 12px;
}
.emptable tr:nth-child(even){background-color: #f2f2f2;}
.emptable tr:hover {background-color: #ddd;}