

.signupForm {
    border: 1px solid red;
    background-color: rgb(241, 179, 104);
    width: 300px;
    margin: auto;
    padding: 10px;
    margin-top: 200px;
    border-radius: 10px;
}

.signupForm .form-group {
    margin-bottom: 10px;
}

.signupForm input {
    width: 100%;
    padding: 6px;
    border-radius: 4px;
}
.signupForm button {
    padding: 10px 24px;
    color: #fff;
    background-color: #5cb85c;
    border-color: #4cae4c;
    border-radius: 4px;
}