.calculator {
    border: 1px solid red;
    background-color: rgb(241, 179, 104);
    width: 300px;
    margin: auto;
    padding: 10px;
    margin-top: 200px;
    text-align: center;
    border-radius: 10px;
}

.calculator button {
    width: 35px;
    height: 35px;
    display: inline-block;
    margin: 2px;
    border-radius: 50%;
}

.calculator input {
    padding: 6px;
    border-radius: 4px;
    margin: 15px 0;
}