#customers {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 90%;
    margin: 50px auto 0;
  }
  
  #customers td, #customers th {
    border: 1px solid #ddd;
    padding: 12px;
  }
  
  #customers tr:nth-child(even){background-color: #e7e7e7;}
  
  #customers tr:hover {background-color: #ddd;}
  
  #customers th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #e69335;
    color: white;
  }
  .controls button{
    background-color: #e69335;
    color: white;
    padding: 10px;
    margin: 5px;
    transition: all 0.5s;
  }
  .controls button.currentpage{
    background-color: black;
    color: fff;
  }
  .controls button:active{
   transform: scale(0.8);
  }